.inputs {
  display: flex;
  flex-direction: column;
  gap: 20px;

  padding: 10px;

  &__field {
    padding-left: 10px;

    border: 1px solid #ccc;
    border-radius: 5px;

    cursor: pointer;

    transition: border 0.5s ease-in;

    &:hover {
      border: 1px solid #000;
    }

    &--button {
      transition: background-color 0.5s ease-in;
      transition: border-color 0.5s ease-in;
  
      &:active {
        background-color: #ccc;
        border-color: #000;
      }
    }
  }
}