.authorization {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;

  margin: 100px auto;

  width: 280px;

  &__field {
    height: 32px;
    width: 100%;

    border: 1px solid #ccc;
    border-radius: 5px;

    cursor: pointer;

    transition: border 0.5s ease-in;

    &:hover {
      border: 1px solid #000;
    }

    transition: background-color 0.5s ease-in;
    transition: border-color 0.5s ease-in;

    &:active {
      background-color: #ccc;
      border-color: #000;
    }

    &__name {
      padding-left: 10px;
    }

    &--error {
      border: 2px solid red;
    }
  }
}

.error-message {
  color: red;
  font-size: 14px;
  margin-top: 5px;
}